// 

import Form, { FormParams, onSubmitForm } from '../elements/form-block';
import Input, { InputParams } from '../elements/input-block';
import Button, { ButtonParams } from '../elements/button-block';
import authorization from './authorization';
import chat from './chat';

const documentTitle = "";

const registrationForm: FormParams = {
	title: ''
};

const registrationInputs: InputParams[] = [
	{
		element: '.reg-form-fieldset',
		id: 'email',
		name: 'email',
		label: 'E-mail',
		value: '',
		type: 'text',
		required: true,
		errorText: '  email',
		validationType: 'email',
		classList: '',
	},
	{
		element: '.reg-form-fieldset',
		id: 'login',
		name: 'login',
		label: '',
		value: '',
		type: 'text',
		required: true,
		errorText: '    3  20  ,   ,     ,  ,  ',
		validationType: 'login',
		classList: '',
	},
	{
		element: '.reg-form-fieldset',
		id: 'secondName',
		name: 'second_name',
		label: '',
		value: '',
		type: 'text',
		required: true,
		errorText: '    ,  ,   ,  ',
		validationType: 'name',
		classList: '',
	},
	{
		element: '.reg-form-fieldset',
		id: 'firstName',
		name: 'first_name',
		label: '',
		value: '',
		type: 'text',
		required: true,
		errorText: '    ,  ,   ,  ',
		validationType: 'name',
		classList: '',
	},
	{
		element: '.reg-form-fieldset',
		id: 'phone',
		name: 'phone',
		label: '',
		value: '',
		type: 'text',
		required: true,
		errorText: '    10  15 ,   ,    ',
		validationType: 'phone',
		classList: '',
	},
	{
		element: '.reg-form-fieldset',
		id: 'password',
		name: 'password',
		label: '',
		value: '',
		type: 'password',
		required: true,
		errorText: '    8  40 ,        ',
		validationType: 'password',
		classList: '',
	},
	{
		element: '.reg-form-fieldset',
		id: 'repeatPassword',
		name: 'repeat_password',
		label: ' ',
		value: '',
		type: 'password',
		required: true,
		errorText: '    8  40 ,        ',
		validationType: 'password',
		classList: '',
	},
];

const registrationButtons: ButtonParams[] = [
	{
		element: '.buttons-wrapper',
		id: '',
		name: '',
		classes: 'add-link',
		onClick: (event) => {
			event.preventDefault();
			onSubmitForm('.reg-form', function(){
				chat();
			});
		},
	},
	{
		element: '.buttons-wrapper',
		id: '',
		name: '',
		classes: 'reg-link',
		onClick: (event) => {
			event.preventDefault();
			authorization();
		},
	},
];

export default function(): void {

	document.title = documentTitle;

	new Form(registrationForm).insertBlock("#app", true);

	registrationInputs.forEach(function(input) {
		new Input(input).insertBlock(input.element);
	});

	registrationButtons.forEach(function(button) {
		new Button(button).insertBlock(button.element);
	});
}